
.main {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.site-title {
  font-family: 'Droid Serif', serif;
  transition: all 1s;
  position: relative;
  top:0;
  color:rgba(20, 10, 20, 0.9);
  padding-right: 0.5em;
  margin-top: 30px;
}
@media (max-width: 500px){
.site-title-main{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0;
    margin: 0;
}
.main{
    -ms-flex-align: center;
        align-items: center;
}
}
.component-container{
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  z-index: -1;
  overflow: hidden;
}
*::-webkit-scrollbar{
  width: 8px;
  background-color: transparent;
  position: relative;
}
/*定义滚动条的轨道，内阴影及圆角*/
*::-webkit-scrollbar-track{
  border-radius: 8px;
  background-color: #f5f5f5;
  position: relative;
  left: -5px;
  transition: all 0.2s;
}
/*定义滑块，内阴影及圆角*/
*::-webkit-scrollbar-thumb{
  background-color: rgba(120,120,120,0.6) ;
  transition: all 0.2s;
}
*::-webkit-scrollbar-track:window-inactive{
  background-color:rgba(0,0,0,0);
}
*::-webkit-scrollbar-thumb:window-inactive{
  background-color: rgba(120,120,120,0.1) ;
}
.component-container>*{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.site-title-main{
  color: rgba(223,145,145,0.4);
  font-size: 4em;
  top:15%;
  text-shadow: 0px 0px 6px rgba(212, 166,50,0.6 );
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
  box-sizing: border-box;
  height:100%;
  overflow: hidden;
}




