
.articlelist-item{
    height: 100%;
    position: relative;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:column;
        flex-direction:column;
    padding: 10px 0;
    text-align: left;
    border-bottom: 1px dotted rgba(50, 50, 50, 0.8)
}
.articlelist-item:hover{
}
.articlelist-title,.articlelist-title:visited{
    color: rgba(0, 0, 0, 1.0);
    text-decoration: none;
    font-size: 24px;
    line-height: 1.4em;
}
.articlelist-brif{
    position: relative;
}
.articlelist-brif>span{
    border-left: 6px solid rgba(180, 180, 100, 0.5);
    padding: 0 1em;
    color: rgba(180, 180, 100, 0.5);
    line-height: 1.4em;
    display: block;
    position: relative;
    left: 0;
    transition:left 0.3s ease,color 0.3s;
    cursor:default;
}
.articlelist-brif-edit>a{
  position: absolute;
  font-size: 1.5em;
  left: 0.5em;
  top: calc(50% - 0.6em);
  text-decoration: none;
  color: rgba(180, 180, 100, 0.5);
  opacity: 0;
  transform: rotateZ(-200deg);
  transition: opacity 0.3s,transform 1s;
}
.articlelist-brif-edit:hover>a{
  opacity: 1;
  transform: rotateZ(360deg)
}
.articlelist-brif-edit:hover>span{
  left:2.8em;
  color: rgba(180, 180, 100, 0.3);
}
.articlelist-meta{
  color:gray;
  text-align: right;
  padding-right: 2em;
  font-size: 0.5em;
  line-height: 2em;
}
.articlelist-meta-commit{
  float: left;
  font-size: 1em;
  color:currentColor;
  text-decoration: none;
}



