
@import url('../../node_modules/medium-editor/dist/css/medium-editor.css');
@import url('../../node_modules/medium-editor/dist/css/themes/default.css');
.article{
    overflow:auto;
    width:100%;
    height:100%;
}
.article-toolbar{
    position:absolute;
    top:0;
    right:0;
}
.article-media{
    position: absolute;
    top:1em;
    right: 2em;
    font-size: 1.5em;
    color: rgba(181, 64, 16, 0.2);
    transition: all 0.5s
}
.article-media:hover{
    color: rgba(181, 64, 16, 0.5);
}
.article-media>input{
    display:none;
}
.edit-button{
    font-size: 1.4em;
    transition:all 0.5s;
    background: none;
    border: none;
    padding:0;
    outline: none;
}
.edit-button-edit{
    color:rgba(181, 64, 16, 0.15);
}
.edit-button-save{
    color:#f20c00;
}
.article,.article-editor{
    min-height: 400px;
    width: 100%;
}
.article-title-contianer{
    position: relative;
    width:30em;
    height: 3em;
    margin: auto;
    display: inline-block;
}
.article-title-contianer>svg{
    position: absolute;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    pointer-events: none;
    border-radius: 4px 4px 4px 0;
}
.article-title-border{
    width: 100%;
    height: 100%;
    stroke-width: 8px;
    stroke: #f20c00;;
    fill:none;
    stroke-dasharray:3em,63em;
    stroke-dashoffset: 3em;
    transition: stroke-dashoffset 0.5s ease,stroke-dasharray 0.5s ease,stroke 0.5s;
}
.article-title-border-edit{
    stroke: rgba(181, 64, 16, 0.15);
    stroke-dasharray:30em,63em;
    stroke-dashoffset: 60em;
}
.article>.article-context{
    position: relative;
    width: 800px;
    margin: 50px auto;
    box-sizing: border-box;
    font-size: 1.2em;
    transition: box-shadow 0.4s;
}
.article-editor{
    box-sizing: border-box;
    text-align: left;
    padding: 3em;
    outline: none;
    transition: box-shadow 0.5s;
}
.article-context-editing{
    box-shadow: 0 0 16px 8px rgba(181, 64, 16, 0.05);
}
.article-context-editing:focus{
    box-shadow: 0 0 16px 8px rgba(181, 64, 16, 0.1)
}
.article-title{
    text-align: left;
    width:100%;
    height: 2em;
    font-size:24px;
    line-height: 2em;
    border:none;
    outline: none;
    padding:0;
    padding-left: 1em;
}
.article-fullscreen{
    position: absolute;
    top:1em;
    right: 1em;
    font-size: 1.5em;
    color: rgba(181, 64, 16, 0.2);
    transition: all 0.5s
}
.article-fullscreen:hover{
    color: rgba(181, 64, 16, 0.5);
    transition: all 0.5s
}
blockquote{
    border-left: 6px solid #f20c00;
    color:#999;
    padding: 16px;
    margin: 0px;
}
html:-webkit-full-screen .site-title,html:-webkit-full-screen .navi{
    display: none;
}
html:-webkit-full-screen .article{
    box-sizing: border-box;
    margin-top: 100px;
    padding-top:100px;
}
html:-webkit-full-screen .article-context{
    margin-bottom: 300px;
}



