
body,html{
  padding: 0;
  margin: 0;
  height: 100%;
  transition: opacity 0.8s linear;
}
#app {
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial;
  -webkit-font-smoothing: antialiased;
  text-align: center;
  filter: blur(8px);    
  transition: filter 1.6s linear;
  pointer-events: none;
  transition:margin 1s;
}
#app,.main,body{
    position: relative;
    z-index: -5;
}
.apploaded{
  filter: none!important;
  pointer-events: inherit!important;
}
.app-to-left{
  margin-left:-100%;
  width:100%;
}
ul{
  padding: 0;
}



