
body{
    transition: background 0.5s;
}
.body-background{
    position: fixed;
    z-index: -1;
    left: 0;
    top:0;
    height: 100%;
    width: 100%;
    background-size: cover;
    opacity: 0;
    transition: opacity 0.2s linear,background 0.2s;
}
.navi-is-menu .navi-item:hover>.body-background{
    opacity: 1.0;
}
.body-background{
    pointer-events:none;
}
.navi{
    width: 100%;     
    position: relative;
    transition: all 1s,box-shadow 0s 0s;
    top:0;
    /*box-shadow: 0px 0px 5px 2px #fff;*/
}
.navi-list{
    width: 100%;
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;        
    font-family:'JuzhenFang90f7448985259';
}
.navi-list a{
    text-decoration: none;
}
.navi-list a,.navi-list a:active,.navi-list a:visited{
    color: currentColor;
}
