.navi-item{ 
    /*white-space: nowrap;overflow: hidden;*/
    -ms-flex:1;
        flex:1;
    transition: flex 1s,opacity 1s,transform 1s,margin 1s;
    transition: flex 1s,opacity 1s,transform 1s,margin 1s,-ms-flex 1s;
    line-height: 45px;
}
.navi-item>span,.navi-item>a{
    padding-bottom: 3px;
}
.navi-is-bar{
    background: transparent!important;
    font-size: 24px;
}
.navi-is-bar a,.route-active{
    border-bottom: 3px solid rgba(0,0,0,0);
    border-radius: 3px;
    transition: color 0.4s,border 0.4s!important;
}
.navi-is-bar a:hover{
    color:#f20c00;
    border-bottom: 3px solid #f20c00;
}
.route-active{
    color:#f20c00!important;
    cursor: default;
    border-bottom-color:#f20c00!important;
}
.navi-is-menu{
    color:white!important;
    top: 45%;
    font-size: 36px;
}
.hide-return{
    -ms-flex:0;
        flex:0;
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    opacity: 0;
    transform: translateX(-100px);
    margin: 0;
}
@media (max-width: 500px){
.background-show{
        display: none;
}
.navi{
        line-height: 14px;
}
.navi-item{
        font-size: 24px;
}
}



